import { Injectable } from '@angular/core'

@Injectable({
  providedIn: 'root',
})
export class PasswordMeterService {
  checkPassword(strPassword: string) {

    const m_strUpperCase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
    const m_strLowerCase = 'abcdefghijklmnopqrstuvwxyz'
    const m_strNumber = '0123456789'
    const m_strCharacters = '!@#$%^&*?_~'

    const countContain = function(strPassword: any, strCheck: any) {
      // Declare variables
      var nCount = 0

      for (let i = 0; i < strPassword.length; i++) {
        if (strCheck.indexOf(strPassword.charAt(i)) > -1) {
          nCount++
        }
      }

      return nCount
    }

    // Reset combination count
    var nScore = 0

    // Password length
    // -- Less than 4 characters
    if (strPassword.length < 5) {
      nScore += 5
    }

    // -- 5 to 7 characters
    else if (strPassword.length > 4 && strPassword.length <= 7) {
      nScore += 10
    }
    // -- 8 or more
    else if (strPassword.length > 7) {
      nScore += 25
    }

    // Letters
    var nUpperCount = countContain(strPassword, m_strUpperCase)
    var nLowerCount = countContain(strPassword, m_strLowerCase)
    var nLowerUpperCount = nUpperCount + nLowerCount

    // -- Letters are all lower case
    if (nUpperCount == 0 && nLowerCount != 0) {
      nScore += 10
    } else if (nUpperCount != 0 && nLowerCount != 0) { // -- Letters are upper case and lower case
      nScore += 20
    }

    // Numbers
    var nNumberCount = countContain(strPassword, m_strNumber)
    // -- 1 number
    if (nNumberCount == 1) {
      nScore += 10
    } else if (nNumberCount >= 2) {
      nScore += 20
    }

    // Characters
    var nCharacterCount = countContain(strPassword, m_strCharacters)
    // // -- 1 character
    if (nCharacterCount == 1) {
      nScore += 10
    }
    // -- More than 1 character
    if (nCharacterCount > 1) {
      nScore += 25
    }

    // Bonus
    // -- Letters and numbers
    if (nNumberCount != 0 && nLowerUpperCount != 0) {
      nScore += 2
    }
    // -- Letters, numbers, and characters
    if (nNumberCount != 0 && nLowerUpperCount != 0 && nCharacterCount != 0) {
      nScore += 3
    }
    // -- Mixed case letters, numbers, and characters
    if (nNumberCount != 0 && nUpperCount != 0 && nLowerCount != 0 && nCharacterCount != 0) {
      nScore += 5
    }

    return nScore
  }

}
