import { Component, OnInit } from '@angular/core'
import { AuthUserService } from '@core/services/auth-user.service'
import { User } from '@core/models/user.model'
import { ApiService } from '@core/services/api.service'
import { PaymentMethod } from '@core/models/paymentMethod.model'
import { Form } from '@core/common/form2'
import { FormBuilder } from '@angular/forms'
import { DateUtil } from '@core/services/date.service'
import { takeUntil } from 'rxjs'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent extends Form implements OnInit {
  public user?: User|null
  public totalAddresses: number
  public address: any
  public numLogins: number
  public method: PaymentMethod
  public totalMethods: number
  public editProfile = false

  public months = DateUtil.months().map(i => ({ label: i, value: i }))
  public years = DateUtil.dobYears().map(i => ({ label: i, value: i }))
  public days = DateUtil.days().map(i => ({ label: i, value: i }))
  public salutations = ['Mr', 'Mrs'].map(i => ({ label: i, value: i }))
  loading = false

  constructor(private authService: AuthUserService, private api: ApiService, fb: FormBuilder) {
    super()
    this._initFormControl(fb, {})
  }

  ngOnInit(): void {
    this.authService.authUser.subscribe((user) => {
      this.user = user
      this.initProfileForm(user)
    })

    this.api.get('user/addresses/featured')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.totalAddresses = data.count
        this.address = data.featured
      })

    this.api.get('user/payment-methods/featured')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.totalMethods = data.count
        this.method = data.featured
      })

    this.api.get('user/recent-logins/count')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.numLogins = data.count
      })
  }

  saveProfile() {
    const dDay = this.form.controls['dobDay'].value || '-'
    const dMonth = this.form.controls['dobMonth'].value || '-'
    const dYear = this.form.controls['dobYear'].value || '-'

    const data = {
      'name': this.form.controls['name'].value,
      'salutation': this.form.controls['salutation'].value,
      'phoneNumber': this.form.controls['phoneNumber'].value,
      'dob': `${dYear}-${dMonth}-${dDay}`
    }

    this.loading = true
    this.api.post('user/save-profile', data)
      .pipe(this.handleSubmit())
      .subscribe(() => {
        this.editProfile = false
        this.loading = false
      })
  }

  private initProfileForm(user?: User|null) {
    if (! user) {
      return
    }

    let date
    if (user.dob) {
      date = new Date(user.dob)
    }

    this.setFCBYName('name', null, user.name)
    this.setFCBYName('salutation', null, user.salutation)
    this.setFCBYName('phoneNumber', null, user.phoneNumber)

    const day = date ? (date.getDate().toString().padStart(2, '0')) : null
    const month = date ? ((date.getMonth() + 1).toString().padStart(2, '0')) : null
    const year = date ? date.getFullYear() : null

    this.setFCBYName('dobDay', null, day)
    this.setFCBYName('dobMonth', null, month)
    this.setFCBYName('dobYear', null, year)
  }
}
