import { Component, OnInit, ViewChild } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { SetPasswordComponent } from '../set-password/set-password.component'
import { Router } from '@angular/router'
import { LoginType } from '@core/types'
import { MicroserviceType } from '@core/enums/microservice-type.enum'

@Component({
  selector: 'app-security',
  templateUrl: './security.component.html',
  styleUrls: ['./security.component.scss']
})
export class SecurityComponent implements OnInit {

  public authLogin: boolean = true
  public pwLogin: boolean = false
  public loading = true

  @ViewChild('input') input:any

  constructor(public api: ApiService, public dialog: MatDialog, private router: Router) {}

  ngOnInit(): void {
    this.api.get('user-info/auth-mode', MicroserviceType.usr, { responseType: 'text' })
      .subscribe((loginType: LoginType) => {
        this.authLogin = loginType === LoginType.Code
        this.pwLogin = loginType === LoginType.Password
        this.loading = false
      })
  }

  activeAuthLogin() {
    if (this.authLogin) {
      return
    }

    this.api.post('user/login-type/set-code', {})
      .subscribe(() => {
        this.authLogin = true
        this.pwLogin = false
      })
  }

  activePwLogin() {
    if (this.pwLogin) {
      return
    }

    this.input.nativeElement.checked = false

    this.router.navigate(['account/security/set-password'])
  }

  start($event: SetPasswordComponent) {
    if ($event.changed) {
      this.pwLogin = $event.changed
      this.input.nativeElement.checked = $event.changed
      this.authLogin = false
    } else {

    }
  }
}
