import { Component } from '@angular/core'
import { Router } from '@angular/router'
import { AuthUserService } from '@core/services/auth-user.service'

@Component({
  selector: 'app-account-layout',
  templateUrl: './account-layout.component.html',
  styleUrls: ['./account-layout.component.scss']
})
export class AccountLayoutComponent  {

  public menus = [
    {
      name: 'Favourite list',
      children: [{ name: 'Favourite list' }],
    },
    {
      name: 'Order history',
      children: [{ name: 'Order history' }],
    },
    {
      name: 'Account details',
      url: 'account',
      children: [
        { name: 'My addresses', url: '/account/addresses' },
        { name: 'Payment method', url: '/account/payment-methods' },
        { name: 'Login & security', url: '/account/security' },
      ],
    }
  ]

  constructor(private authService: AuthUserService, private router: Router) { }

  logout() {
    this.authService.logoutAuthUser()
    this.router.navigate(['/'])
  }
}
