import { Component, OnInit } from '@angular/core'
import { map, Observable, Subject, switchMap } from 'rxjs'
import { Login } from '@core/models/login.model'
import { ApiService } from '@core/services/api.service'

@Component({
  selector: 'app-logins',
  templateUrl: './logins.component.html',
  styleUrls: ['./logins.component.scss']
})
export class LoginsComponent implements OnInit {

  recentLogins: Array<Login> = []
  logins$: Observable<any>
  public moreLogins: number
  private loadLogins$ = new Subject<any>()
  private page = 0

  constructor(private api: ApiService) { }

  ngOnInit(): void {

    this.logins$ = this.loadLogins$.pipe(
      switchMap(page => {
        return this.api.get(`user/recent-logins?page=${page}`).pipe(
          map(({ data: { logins, more } }) => {
            this.recentLogins.push(...logins)
            this.moreLogins = more
            return this.recentLogins
          }))
      }
      ))

    setTimeout(() => this.loadLogins())

  }

  loadLogins() {
    this.loadLogins$.next(++this.page)
  }

}
