import { Component, Inject, OnInit } from '@angular/core'
import { FormBuilder, Validators } from '@angular/forms'
import { FormValidators } from '@core/utils/form-validators'
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog'
import { PasswordMeterService } from '@core/services/password-meter.service'
import { Form } from '@core/common/form2'

@Component({
  selector: 'app-popup',
  templateUrl: './popup.component.html',
  styleUrls: ['./popup.component.scss']
})
export class PopupComponent extends Form {

  public pwStrength = 0

  protected formFields = {
    password: ['', [Validators.required, FormValidators.passwordStrength, Validators.minLength(8)]],
    repeatPassword: ['', [Validators.required]]
  }

  protected validators = [FormValidators.match('password', 'repeatPassword')]

  constructor(
    public dialogRef: MatDialogRef<PopupComponent>,
    fb: FormBuilder,
    private pwMeter: PasswordMeterService
  ) {
    super()

    this._initFormControl(fb, this.formFields, this.validators)

    this.form.get('password')?.valueChanges.subscribe(x => {
      this.pwStrength = this.pwMeter.checkPassword(x)
    })

  }

  save() {
    this.form.markAsTouched()
    if (this.form.valid) {
      this.dialogRef.close(this.form.controls['password'].value)
    }
  }

}
