import { Component, OnInit } from '@angular/core'
import { filter, takeUntil } from 'rxjs'
import { ApiService } from '@core/services/api.service'
import { AutoDestroy } from '@core/common/auto-destroy'
import { PaymentMethod } from '@core/models/paymentMethod.model'
import { ConfirmComponent } from 'src/app/shared/components/confirm/confirm.component'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router'
import { AddPaymentComponent } from './add/add.component'

@Component({
  selector: 'app-payment-methods',
  templateUrl: './payment-methods.component.html',
  styleUrls: ['./payment-methods.component.scss']
})
export class PaymentMethodsComponent extends AutoDestroy implements OnInit {

  public methods: Array<PaymentMethod>

  constructor(private api: ApiService, private dialog: MatDialog, private router: Router, private route: ActivatedRoute) {
    super()

    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(() => {
        const mode = this.route.snapshot.params['id']

        if (mode === 'add') {
          this.showAddDialog()
        }
      })

  }

  ngOnInit(): void {
    this.api.get('user/payment-methods')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.methods = data
      })
  }

  showAddDialog() {
    const method = PaymentMethod.adapt({})
    const dialogRef = this.dialog.open(AddPaymentComponent, {
      width: '380px',
      data: method,
    })

    dialogRef.afterClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe((method: PaymentMethod) => {
        this.handleAddAddr(method)
      })
  }

  private handleAddAddr(method: PaymentMethod) {
    if (!method) {
      this.router.navigate(['/account/payment-methods/'])
      return
    }

    this.api.post('user/payment-methods/save', { method })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.methods.push(data)
        this.router.navigate(['/account/payment-methods/'])
      })
  }

  markAsFeatured(index: number) {

    const address = this.methods[index]

    this.api.post(`user/payment-methods/mark-featured/${address.id}`, {})
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        address.featured = true

        const oldFeatured = this.methods.find(x => x.featured)
        if (oldFeatured) {
          oldFeatured.featured = false
        }
      })
  }

  delete(index: number) {
    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '350px',
      data: { label: 'payment method' }
    })

    dialogRef.beforeClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe(canDelete => {
        this.handleDeleteAddr(canDelete, index)
      })
  }

  private handleDeleteAddr(canDelete: Boolean, index: number) {
    if (!canDelete) {
      return
    }

    const address = this.methods[index]

    this.api.delete(`user/payment-methods/delete/${address.id}`)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.methods.splice(index, 1)
      })
  }

}
