import { Component, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { Address } from '@core/models/address.model'
import { filter, takeUntil } from 'rxjs'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { ConfirmComponent } from 'src/app/shared/components/confirm/confirm.component'
import { AutoDestroy } from '@core/common/auto-destroy'
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router'
import { EditAddressComponent } from '../edit/edit.component'

@Component({
  selector: 'app-addresses',
  templateUrl: './addresses.component.html',
  styleUrls: ['./addresses.component.scss']
})
export class AddressesComponent extends AutoDestroy implements OnInit {

  public addresses: Array<Address>

  private addressId: string

  constructor(private api: ApiService, private dialog: MatDialog, private router: Router, private route: ActivatedRoute) {
    super()

    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(() => {
        this.addressId = this.route.snapshot.params['id']

        if (!this.addressId) {
          return
        }

        if (this.addressId === 'add') {
          this.showAddDialog()
          return
        }

        if (this.addresses) {
          this.showEditDialog(parseInt(this.addressId))
        }
      })

  }

  ngOnInit(): void {
    this.api.get('user/addresses')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.addresses = data

        if (this.addressId && this.addressId != 'add') {
          this.showEditDialog(parseInt(this.addressId))
        }
      })
  }

  delete(index: number) {
    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '350px',
      data: { label: 'address' }
    })

    dialogRef.beforeClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe(canDelete => {
        this.handleDeleteAddr(canDelete, index)
      })
  }

  markAsFeatured(index: number) {

    const address = this.addresses[index]

    this.api.post(`user/addresses/mark-featured/${address.id}`, {})
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        address.featured = true

        const oldFeatured = this.addresses.find(x => x.featured)
        if (oldFeatured) {
          oldFeatured.featured = false
        }
      })
  }

  showEditDialog(id: number) {
    const index = this.addresses.findIndex(x => x.id === id)
    const address = this.addresses[index]

    const dialogRef = this.dialog.open(EditAddressComponent, {
      panelClass: 'responsive-dialog',
      data: { mode: 'edit', address },
    })

    dialogRef.afterClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe((address: Address) => {
        this.handleEditAddr(address, index)
      })
  }

  showAddDialog() {
    const address = Address.adapt({})
    const dialogRef = this.dialog.open(EditAddressComponent, {
      panelClass: 'responsive-dialog',
      data: { mode: 'add', address },
    })

    dialogRef.afterClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe((address: Address) => {
        this.handleAddAddr(address)
      })
  }

  private handleEditAddr(address: Address, index: number) {
    if (!address) {
      this.router.navigate(['/account/addresses/'])
    }

    this.api.put(`user/addresses/update/${address.id}`, { address })
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.addresses[index] = address
        this.router.navigate(['/account/addresses/'])
      })
  }

  private handleAddAddr(address: Address) {
    if (!address) {
      this.router.navigate(['/account/addresses/'])
    }

    this.api.post('user/addresses/save', { address })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.addresses.push(data)
        this.router.navigate(['/account/addresses/'])
      })
  }

  private handleDeleteAddr(canDelete: Boolean, index: number) {
    if (!canDelete) {
      return
    }

    const address = this.addresses[index]

    this.api.delete(`user/addresses/delete/${address.id}`)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.addresses.splice(index, 1)
      })
  }
}
