import { Component, OnInit } from '@angular/core'
import { BreakpointObserver } from '@angular/cdk/layout'
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router'
import { filter } from 'rxjs'

@Component({
  selector: 'app-route-breadcrumbs',
  templateUrl: './route-breadcrumbs.component.html',
  styleUrls: ['./route-breadcrumbs.component.scss']
})
export class RouteBreadcrumbsComponent {

  public menuItems: any

  public constructor(private breakpointObserver: BreakpointObserver, private router: Router, private route: ActivatedRoute) {
    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(() => {
        this.menuItems = this.createBreadcrumbs(this.route.root)
      })
  }

  private createBreadcrumbs(route: ActivatedRoute, url: string = '', breadcrumbs: any = []): any {
    const children: ActivatedRoute[] = route.children

    if (children.length === 0) {
      return breadcrumbs
    }

    for (const child of children) {
      const routeURL: string = child.snapshot.url.map(segment => segment.path).join('/')
      if (routeURL !== '') {
        url += `/${routeURL}`
      }

      const label = child.snapshot.data['label']
      if (label) {
        breadcrumbs.push({ label, url })
      }

      return this.createBreadcrumbs(child, url, breadcrumbs)
    }
  }

}
