import { Component, OnInit } from '@angular/core'
import { map, Observable, take, tap } from 'rxjs'
import { Question } from '@core/models/question.model'
import { FormBuilder } from '@angular/forms'
import { ApiService } from '@core/services/api.service'
import { Form } from '@core/common/form2'
import { MicroserviceType } from '@core/enums/microservice-type.enum'

@Component({
  selector: 'app-security-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.scss']
})
export class QuestionsComponent extends Form implements OnInit {

  panelOpenState: boolean = true
  questions$: Observable<any>
  public answeredCount: number = 0
  public questionsCompleted: boolean = true

  constructor(fb: FormBuilder, private api: ApiService) {
    super()
    this._initFormControl(fb, {})
  }

  ngOnInit(): void {
    this.questions$ = this.api.get('security-questions/answers', MicroserviceType.au)
      .pipe(
        // map(({ data }) => data),
        tap(( questions ) => {

          (questions as []).forEach(
            (question: Question) => {
              this.setFCBYName(question.questionId, null, question.answer, ['required'])

              this.questionsCompleted = this.questionsCompleted && !!question.answer
              this.answeredCount += question.answer ? 1 : 0
            }
          )
        })
      )
  }

  public saveAnswers() {
    debugger
    const answers: Array<object> = []
    this.questionsCompleted = false
    this.answeredCount = 0

    for (let [key, value] of Object.entries(this.form.controls)) {

      this.answeredCount += value.value ? 1 : 0

      answers.push({
        'questionId': key,
        'answer': value.value
      })
    }

    this.questionsCompleted = this.answeredCount === answers.length

    this.api.put('security-questions/answers', { answers }, MicroserviceType.au)
      .pipe(take(1))
      .subscribe()
  }

}
