import { Injectable } from '@angular/core'
import { Language } from 'src/app/core/enums/language.enum'
import { TranslateLoader } from '@ngx-translate/core'
import { of } from 'rxjs'

import enShared from 'src/assets/i18n/shared/en.json'
import roShared from 'src/assets/i18n/shared/ro.json'

@Injectable({
  providedIn: 'root',
})
export class AccountTranslateLoader implements TranslateLoader {
  getTranslation(lang: string) {
    switch (lang) {
        case Language.En:
          return of({ ...enShared })
        case Language.Ro:
          return of({ ...roShared })
        default:
          throw Error(`Unsupported language: ${lang}`)
    }
  }
}
