import { NgModule } from '@angular/core'
import { RouterModule, Routes } from '@angular/router'
import { AuthGuard } from '@core/guards/auth.guard'
import { AccountComponent } from './account.component'
import { HomeComponent } from './pages/home/home.component'
import { SecurityComponent } from './pages/security/index/security.component'
import { SetPasswordComponent } from './pages/security/set-password/set-password.component'
import { AddressesComponent } from './pages/addresses/index/addresses.component'
import { PaymentMethodsComponent } from './pages/payment-methods/payment-methods.component'

const routes: Routes = [
  {
    path: '',
    component: AccountComponent,
    data: { label: 'My Account' },
    children: [
      {
        path: '',
        component: HomeComponent,
        canActivate: [AuthGuard],
        data: { label: '' },
      },
      {
        path: 'addresses',
        redirectTo: 'addresses/',
        pathMatch: 'full'
      },
      {
        path: 'addresses/:id',
        component: AddressesComponent,
        canActivate: [AuthGuard],
        data: { label: 'My Addresses' },
      },
      {
        path: 'payment-methods',
        redirectTo: 'payment-methods/',
        pathMatch: 'full'
      },
      {
        path: 'payment-methods/:id',
        component: PaymentMethodsComponent,
        canActivate: [AuthGuard],
        data: { label: 'Payment methods' },
      },
      {
        path: 'security',
        component: SecurityComponent,
        canActivate: [AuthGuard],
        data: { label: 'Login & security' },
        children: [
          {
            path: 'set-password',
            component: SetPasswordComponent,
            data: { label: 'Password security' }
          },
        ]
      },
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccountRoutingModule { }
