export class PaymentMethod {
  id: number
  name: string
  month: string
  year: string
  cvv: string
  lastFour: number
  cardNumber: string
  featured: boolean

  public static adapt(item: any): PaymentMethod {
    const addr: PaymentMethod = new PaymentMethod()
    addr.id = item.id
    addr.name = item.name
    addr.month = item.month
    addr.year = item.year
    addr.cvv = item.cvv
    addr.cardNumber = item.cardNumber
    addr.lastFour = item.lastFour
    addr.featured = item.featured

    return addr
  }

}
