export class DateUtil {
  public static days(): Array<string> {
    return Array.from({ length: 31 }, (x, i) => {
      return ((String) (i + 1)).padStart(2, '0')
    })
  }

  public static months(): Array<string> {
    return Array.from({ length: 12 }, (x, i) => {
      return ((String) (i + 1)).padStart(2, '0')
    })
  }

  public static years(length: number, digits: number = 2, startFrom: number|null = null) {
    startFrom = startFrom || this.currYear(digits)
    return Array.from({ length }, (x, i) => (startFrom as number) - i)
  }

  public static dobYears() {
    return this.years(40, 4, this.currYear(4) - 20)
  }

  public static currYear(digits: number = 2): number {
    digits = 4 - digits
    return parseInt(new Date().getFullYear().toString().slice(digits))
  }
}
