import { Component, Inject, OnInit } from '@angular/core'
import { Form } from '@core/common/form2'
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog'
import { FormBuilder } from '@angular/forms'
import { Address } from '@core/models/address.model'
import { PaymentMethod } from '@core/models/paymentMethod.model'

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddPaymentComponent extends Form {

  public months = Array.from({ length: 12 }, (x, i) => ({ label: i + 1, value: i+1 }))
  private currYear = (parseInt(new Date().getFullYear().toString().slice(2)))
  public years = Array.from({ length: 5 }, (x, i) => ({ label: this.currYear+i, value: this.currYear+i }))

  constructor(
    private dialogRef: MatDialogRef<AddPaymentComponent>,
    fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public method: PaymentMethod
  ) {
    super()
    this._initFormControl(fb, {})

    Object.keys(method)
      .forEach(k => this.setFCBYName(k, null, method[k as keyof PaymentMethod] as any))
  }

  cancel() {
    this.dialogRef.close(null)
  }

  save() {
    Object.keys(this.method).forEach((k: string) => {
      const val = this.form.controls[k].value
      this.method[k as keyof PaymentMethod] = val as never
    })

    this.dialogRef.close(this.method)

  }
}
